import React from 'react';
import ReactDOM from 'react-dom';
import './index.css'

const DirectoryView = (props) => (
    <div>
        <SearchForm recipes={props.recipes} searchVal={props.searchVal} handleChange={props.handleChange} handleFilter={props.handleFilter}/>
        {props.recipes.map((recipe, i) => <RecipeCard name={recipe.name} key={i} />)}
    </div>
);

const SearchForm = (props) => (
    <form className="form">
        <input className="inputField" value={props.searchVal} onChange={props.handleChange} />
        <button className="searchButton" onClick={props.handleFilter}>Search</button>
    </form>
);

const RecipeCard = (props) => (
    <div>
        <p>{props.name}</p>
    </div>
);

const DetailView = (props) => (
    <div className="detailView">
      <h2>{props.recipe.name}</h2>
      <h3>{props.recipe.ingredients.map((ingredient) => ingredient + ' \n')}</h3>
      <h3>{props.recipe.instructions.map((instruction) => instruction + ' \n')}</h3>
      </div>
);

class App extends React.Component {
    state = {
        recipes: [
            {
                id: 1,
                name: 'Turkey Mayhem',
                ingredients: ['Turkey', 'Mustard', 'Greens', 'Celery', 'Potato'],
                instructions: ['Pre-heat over to 350 degrees', 'Tickle the turkey', 'Cut some celery']
            },
            {
                id: 2,
                name: 'Mac and Cheese',
                ingredients: ['Mac', 'Cheese'],
                instructions: ['Put the mac in the cheese', 'Swish it around', 'Bake for 15 minutes at 350 degrees']
            },
            {
                id: 3,
                name: 'Spanakopita',
                ingredients: ['Olive oil', 'Onion', 'Garlic', 'Spinach'],
                instructions: ['Put the stuff in a bowl', 'Cook the stuff']
            },
            {
                id: 4,
                name: 'Vasilopita',
                ingredients: ['Flower', 'Baking Soda', 'Butter', 'Sugar', 'Eggs'],
                instructions: ['Throw everything in the air and hope it comes together', 'Knead the bread', 'Show the bread some love', 'Bake it at 400 degrees']
            }
        ],
        searchVal: '',
        selectedRecipes: [{id: 4,
            name: '',
            ingredients: [''],
            instructions: ['']
        }]
    }

    handleChange = (event) => {
        this.setState({ searchVal: event.target.value });
    }

    selectRecipes = (event) => {
        event.preventDefault();
        const relevantRecipes = this.state.recipes.filter(recipe => recipe.name.includes(this.state.searchVal));
        this.setState({selectedRecipes: relevantRecipes});
    }


    render() {
        return (
            <div>
                <h1 className="title">Recipe Finder</h1>
            <DirectoryView 
                recipes={this.state.selectedRecipes}
                searchVal={this.state.searchVal}
                handleChange={this.handleChange}
                handleFilter={this.selectRecipes}
            />
            <DetailView
            recipe={this.state.selectedRecipes[0]}
            />
            </div>
        );
    }
}

ReactDOM.render(<App />, document.getElementById('root'));